a.rwp-tooltips {
  position: relative;
  display: inline-block;
  margin-right: 5px;
	
}
a.rwp-tooltips span {
  position: absolute;
  width:140px;
  color: #FFFFFF;
  background: #212121;
  height: 27px;
  line-height: 27px;
  text-align: center;
  visibility: hidden;
  border-radius: 25px;
}
a.rwp-tooltips span:after {
  content: '';
  position: absolute;
  top: 100%;
  left: 50%;
  margin-left: -8px;
  width: 0; height: 0;
  border-top: 6px solid #212121;
  border-right: 8px solid transparent;
  border-left: 8px solid transparent;
}
a:hover.rwp-tooltips span {
  visibility: visible;
  opacity: 1;
  bottom: 26px;
  left: 50%;
  margin-left: -70px;
  z-index: 999;
}

