$iconSize: 20px;

.rwp-str {
    @include clearfix;
    //margin-right: 120px;

    .rwp-s {
        float: left;
        display: block;
        width: ($iconSize / 2);
        height: $iconSize;
        background-image:  url(../images/rating-star.png);
        background-repeat: no-repeat;
        background-size: $iconSize;
        margin: 0;
        padding: 0;


        &.rwp-o.rwp-x {
            background-position: 0px (-$iconSize);
        }

        &.rwp-o.rwp-f {
            background-position: 0px 0px;
        }

        &.rwp-e.rwp-x {
            background-position: (-($iconSize / 2)) (-$iconSize);
        }

        &.rwp-e.rwp-f {
            background-position: (-($iconSize / 2)) 0px;
        }
    }
}

